body {
  margin: 0;
  padding: 0;
  background-color: hsl(0, 0%, 98%);
  font-family: "Lora", serif;
  color: hsl(0, 0%, 13%);
}

.page {
  padding: 1vmin;
  max-width: 800px;
  margin: auto;
  margin-bottom: 5vmin;
}

p {
  margin: 3vmin;
}

.big {
  font-size: 6.3vmin;
  font-weight: 700;
  line-height: 1.3;
  margin-bottom: 5vmin;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

.body {
  font-size: 4.33vmin;
  font-weight: 400;
  line-height: 1.4;
}

.rocket {
  text-align: center;
  cursor: crosshair;
}

.rocket .glyph.animate {
  -webkit-animation: 7s rocket linear;
          animation: 7s rocket linear;
  display: inline-block;
}

@-webkit-keyframes rocket {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  7%,
  14% {
    /* rotated. hold before launch */
    -webkit-transform: rotate(680deg);
            transform: rotate(680deg);
  }
  19% {
    /* mini launch */
    -webkit-transform: translateY(-20px) rotate(680deg);
            transform: translateY(-20px) rotate(680deg);
  }
  25% {
    /* high in the sky */
    -webkit-transform: translateY(-3000px) rotate(680deg);
            transform: translateY(-3000px) rotate(680deg);
    opacity: 1;
  }
  26% {
    /* hide */
    opacity: 0;
  }
  27% {
    /* high in the sky, rotated to be just 40degs off */
    -webkit-transform: translateY(400px) rotate(-40deg);
            transform: translateY(400px) rotate(-40deg);
    opacity: 0;
  }
  70%,
  75% {
    /* landed */
    opacity: 1;
    -webkit-transform: rotate(-40deg);
            transform: rotate(-40deg);
  }
  90%,
  100% {
    /* initial position */
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes rocket {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  7%,
  14% {
    /* rotated. hold before launch */
    -webkit-transform: rotate(680deg);
            transform: rotate(680deg);
  }
  19% {
    /* mini launch */
    -webkit-transform: translateY(-20px) rotate(680deg);
            transform: translateY(-20px) rotate(680deg);
  }
  25% {
    /* high in the sky */
    -webkit-transform: translateY(-3000px) rotate(680deg);
            transform: translateY(-3000px) rotate(680deg);
    opacity: 1;
  }
  26% {
    /* hide */
    opacity: 0;
  }
  27% {
    /* high in the sky, rotated to be just 40degs off */
    -webkit-transform: translateY(400px) rotate(-40deg);
            transform: translateY(400px) rotate(-40deg);
    opacity: 0;
  }
  70%,
  75% {
    /* landed */
    opacity: 1;
    -webkit-transform: rotate(-40deg);
            transform: rotate(-40deg);
  }
  90%,
  100% {
    /* initial position */
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

a {
  text-decoration: none;
  color: inherit;
  background: none;
  -webkit-transition: color 300ms ease-out, background-color 300ms ease-out;
  -o-transition: color 300ms ease-out, background-color 300ms ease-out;
  transition: color 300ms ease-out, background-color 300ms ease-out;
  -webkit-box-shadow: 0 -1px rgba(0, 0, 0, 0.2) inset;
          box-shadow: 0 -1px rgba(0, 0, 0, 0.2) inset;
}

a:hover {
  background: hsl(0, 0%, 13%) !important;
  color: hsl(0, 0%, 98%) !important;
}

::-moz-selection {
  background: hsl(351, 85%, 82%);
}

::selection {
  background: hsl(351, 85%, 82%);
}



